import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, Subject, throwError, TimeoutError } from 'rxjs';
import { catchError, retry, timeout } from 'rxjs/operators';
import { Platform } from '@ionic/angular';

import { IUnit } from '../models/IUnit';
import { environment } from './../configuration/enviroment';
import { IPO } from '../models/IPO';
import { IProduct } from '../models/IProduct';
import { IUser } from '../models/IUser';
import { IUnitList } from '../models/IUnitList';
import { IAuditLog } from '../models/IAuditLog';

const RESPONSE_TIMEOUT: number = 3000;
@Injectable({
  providedIn: 'root'
})

export class HttpService {
  uri: string;
  platforms: string[];
  plat: string;

  constructor(private http: HttpClient, public platform: Platform) {
    this.platforms = this.platform.platforms();
    for (let index = 0; index < this.platforms.length; index++) {
      if (this.platforms[index] == 'mobileweb' || this.platforms[index] == 'pwa') {
        this.plat = 'web';
        break;
      }
    }
    if(this.plat === 'web'){
      this.uri = environment.apiUrl_local
    } else {
      this.uri = environment.apiURL_mobile
    }
  }

  post(serviceName: string, data: any): Observable<any> {
    const url = this.uri + serviceName;
    return this.http
      .post(url, data)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  }

  logOut(token: string): Observable<any> {
    const url = this.uri + 'auth/logout';
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    return this.http
      .post(url, undefined, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  }

  getActivePO(): Observable<any> {
    const url = this.uri + 'pos/open';
    console.log(url);
    return this.http
      .get(url)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  }

  getPOs(): Observable<IPO[]> {
    const url = this.uri + `pos`;
    console.log(url);
    return this.http
      .get<IPO[]>(url)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  getPOState(state: number): Observable<IPO[]> {
    const url = this.uri + `pos/state/${state}`;
    console.log(url);
    return this.http
      .get<IPO[]>(url)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  getPO(id: string): Observable<IPO> {
    const url = this.uri + `pos/${id}`;
    console.log(url);
    return this.http
      .get<IPO>(url)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  createPO(po: IPO, token: string): Observable<any> {
    const url = this.uri + `pos`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .post(url, po, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 
  
  updatePO(po: IPO, token: string): Observable<any> {
    const url = this.uri + `pos/${po.id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .patch(url, po, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  closePO(id: number, token: string): Observable<any> {
    let body = {"id": id};
    const url = this.uri + `pos/close/${id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .post(url, body, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  openPO(id: number, token: string): Observable<any> {
    let body = {"id": id};
    const url = this.uri + `pos/open/${id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .post(url, body, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  deletePO(id: number, token: string): Observable<any> {
    const url = this.uri + `pos/${id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .delete(url, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  getProducts(): Observable<IProduct[]> {
    const url = this.uri + `products`;
    console.log(url);
    return this.http
      .get<IProduct[]>(url)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  }

  getProduct(id: string): Observable<IProduct> {
    const url = this.uri + `products/${id}`;
    console.log(url);
    return this.http
      .get<IProduct>(url)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  createProduct(product: IProduct, token: string): Observable<any> {
    const url = this.uri + `products`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .post(url, product, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  updateProduct(product: IProduct, token: string): Observable<any> {
    const url = this.uri + `products/${product.id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .patch(url, product, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  deleteProduct(id: number, token: string): Observable<any> {
    const url = this.uri + `products/${id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .delete(url, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  getAuditTrail(token: string, skip: number, take: number): Observable<IAuditLog[]> {
    const url = this.uri + `audit-trail?skip=${skip}&take=${take}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .get<IAuditLog[]>(url, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  getUsers(token: string): Observable<IUser[]> {
    const url = this.uri + `users`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .get<IUser[]>(url,options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  getUser(id, token: string): Observable<IUser> {
    const url = this.uri + `users/${id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .get<IUser>(url,options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 
  
  createUser( user:IUser, token: string ): Observable<IUser> {
    const url = this.uri + `auth/register`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .post<IUser>(url, user, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  };

  updateUser(user: IUser, token: string): Observable<IUser> {
    const url = this.uri + `users/${user.id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .patch<IUser>(url, user, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  };

  deleteUser(id: number, token: string): Observable<any> {
    const url = this.uri + `users/${id}`;
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });
    const options = { headers: headers};
    console.log(url);
    return this.http
      .delete(url, options)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  } 

  getUnitListAndStats(units: number, batchId: number): Observable<IUnitList> {
    const url = this.uri + `units/list/stats/${units}/${batchId}`;
    console.log(url);
    return this.http
      .get<IUnitList>(url)
      .pipe(timeout(RESPONSE_TIMEOUT), catchError(this.handleError))
  }

  private handleError(error: HttpErrorResponse) {
    let errorMsg: string;
    if (error instanceof TimeoutError) {
      return throwError('El servicio de backend no responde. Verifique su conexión.');
    }
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.log('Error cliente:', error.error.message);
      errorMsg = `Error:' ${error.error.message}`;
    } else {
      // The backend returned an unsuccessful response code.
      switch (error.status) {
        case 400:{
          errorMsg = error.error.message;
        }
        break;
        case 404:{
            errorMsg = `Archivo no encontrado.`;
          }
          break;
        case 401:{
            errorMsg = `Acceso no autorizado`;
          }
          break;
        case 403:{
            errorMsg = `Acceso denegado.`;
          }
          break;
        case 500:{
            errorMsg = `Error interno del servidor.`;
          }
          break;
        default:{
            errorMsg = `Error del servidor desconocido.`;
          }
          break;
      }
      console.log(
        `Backend returned code ${error.status}, ` + `body was: ${error.error.message}`
      );
    }
    // Return an observable with a user-facing error message.
    return throwError(errorMsg);
  }
}